:root {

    --morado: #6f05ec;
    --negro: #22262b;
    --grid-claro: #787879;
    --grid-mas-claro: #d4d4d4;
    --nombre-persona: #0e0f13;
    --gris-oscuro: #26272e;
    --logo-wpp: #25D366;
    --blanco: white;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: roboto;
    scroll-behavior: smooth;

}

.contenedor-grid {

    display: grid;
    grid-auto-columns: 1fr;
    grid-auto-rows: auto;
    gap: 1rem;



}

body {

    padding: 2rem 5rem;
    position: relative;
}

.header {

    grid-column: 1/5;
    grid-row: 1/2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .8rem;
    position: sticky;
    top: 10px;
    z-index: 1000;
    background-color: var(--morado);
    transition: top .5s;
    margin-bottom: .2rem;

}



.nombre-persona {

    color: var(--blanco);
    font-weight: 600;
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    gap: .2rem;
}

.nombre-persona span {
    color: var(--blanco);
}

.nombre-persona h4 {
    font-size: clamp(.3rem, 2vw, 1rem);
    color: var(--blanco);
}

.profile-icon {

    font-weight: 700;
    color: var(--blanco);
    font-size: clamp(.9rem, 4vw, 1.5rem);
}

.header-menu ul {

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    font-size: clamp(.9rem, 4vw, 1.5rem);


}

.header-menu ul li {

    list-style: none;
    font-weight: 600;


}

.header-menu ul li a {


    text-decoration: none;
    color: var(--blanco);
    font-weight: 700;

}

.home {
    color: var(--blanco);
    font-weight: 900;

}

.main-home {

    grid-column: 1/5;
    grid-row: 2/5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 4rem;

}

.contenido-principal-home {

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;


}

.contenido-principal-home-info {

    display: flex;
    flex-direction: column;
    gap: 1rem;
    flex: 1;


}

.contenido-principal-home-info-nombre {
    font-weight: 400;
}

.manito {

    color: var(--morado);
    font-size: larger;
}

.contenido-principal-home-info-carrera {

    font-size: clamp(1.2rem, 6vw, 5rem);
    font-weight: bold;
    font-weight: 800;
    color: var(--gris-oscuro);
}

.contenido-principal-home-info-descripcion {

    color: var(--grid-claro);
    font-weight: 500;
    font-size: clamp(1rem, 3vw, 1.5rem);
    gap: 2rem;
    width: 100%;


}

.contenido-principal-home-info-descripcion-about {

    color: var(--grid-claro);
    font-weight: 500;
    font-size: clamp(1rem, 3vw, 1.5rem);
    gap: 2rem;
    width: 100%;


}

.hard-skills, .soft-skills {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
}

.hard-skills h2, .soft-skills h2 {
    color: var(--gris-oscuro);
    font-size: clamp(1.5rem, 4vw, 2rem);
    font-weight: 700;
}

.hard-skills ul, .soft-skills ul {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    flex-wrap: wrap;
}

.hard-skills ul li, .soft-skills ul li {
    list-style: none;
    font-size: clamp(2rem, 5vw, 3rem);
    color: var(--morado);
}

.proyectos-contenido {

    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    justify-content: center;
}

.proyectos-contenido-header {
    text-align: center;
    max-width: 600px;
}

.proyectos-contenido-header h2 {
    font-size: clamp(2rem, 6vw, 3rem);
    color: var(--gris-oscuro);
    font-weight: 800;
    margin-bottom: 1rem;
}

.proyectos-contenido-header p {
    color: var(--grid-claro);
    font-size: clamp(1rem, 3vw, 1.2rem);
    line-height: 1.5;
}

.proyectos-contenido-cards {

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    flex-wrap: wrap;
}

.proyectos-contenido-card {
    background-color: var(--grid-mas-claro);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 400px;
    max-width: 100%;
    gap: 1rem;
    padding: 2rem;
    border-radius: 20px;


}

.proyectos-contenido-card img {

    width: 200px;
    max-width: 100%;
    object-fit: cover;
    aspect-ratio: 1/1;
    min-height: 100px;
}

.proyectos-contenido-card-descripcion {

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: .4rem;
    flex-grow: 1;
}

.proyectos-contenido-card-descripcion h3 {
    font-size: clamp(1.2rem, 3vw, 1.5rem);
    color: var(--gris-oscuro);
    font-weight: 700;
}

.proyectos-contenido-card-descripcion p {
    color: var(--grid-claro);
    font-size: clamp(.9rem, 2vw, 1rem);
    text-align: center;
    line-height: 1.4;
}

.proyectos-contenido-card-btns {

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: .4rem;
    width: 100%;

}


.btn-repositorio-encriptador,
.btn-repositorio-youtube-azul {

    padding: clamp(.5rem, 2vw, .9rem);
    width: 100%;
    border-radius: 15px;
    border: none;
    background-color: var(--grid-claro);
    color: white;
    transition: background-color .5s, color .5s, transform .7s;

}

.btn-repositorio-youtube-azul:hover,
.btn-repositorio-encriptador:hover {

    background-color: var(--morado);
    color: white;
    transform: scale(.95);

}

.btn-demo-youtube,
.btn-demo-encriptador {

    padding: clamp(.5rem, 2vw, .9rem);
    width: 100%;
    border-radius: 15px;
    border: 1px solid var(--morado);
    background-color: var(--grid-mas-claro);
    transition: background-color .5s, color .5s, transform .7s;

}

.btn-demo-youtube:hover,
.btn-demo-encriptador:hover {

    background-color: var(--morado);
    transform: scale(.95);
    color: white;

}

.footer {

    grid-column: 1/5;
    grid-row: 3/4;
    background-color: var(--gris-oscuro);
    color: var(--blanco);
    padding: 2rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.footer-contenido {

    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
}

.footer-about h3,
.footer-links h3,
.footer-contacto h3 {
    color: var(--blanco);
    margin-bottom: 1rem;
    font-size: 1.2rem;
}

.footer-about p {
    color: var(--grid-claro);
    line-height: 1.5;
}

.footer-links ul {
    list-style: none;
    padding: 0;
}

.footer-links ul li {
    margin-bottom: .5rem;
}

.footer-links ul li a {
    color: var(--grid-claro);
    text-decoration: none;
    transition: color .3s;
}

.footer-links ul li a:hover {
    color: var(--morado);
}

.footer-contacto p {
    margin-bottom: .5rem;
    color: var(--grid-claro);
}

.footer-contacto a {
    color: var(--grid-claro);
    text-decoration: none;
}

.footer-redes {
    display: flex;
    gap: 1rem;
    margin-top: 1rem;
}

.footer-redes a {
    color: var(--grid-claro);
    font-size: 1.5rem;
    transition: color .3s;
}

.footer-redes a:hover {
    color: var(--morado);
}

.footer-copy {
    text-align: center;
    border-top: 1px solid var(--grid-claro);
    padding-top: 1rem;
    color: var(--grid-claro);
}

.wpp-flotante {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 1000;
}

.wpp-flotante-contenido {
    background-color: var(--logo-wpp);
    border-radius: 50%;
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    transition: transform .3s;
}

.wpp-flotante-contenido:hover {
    transform: scale(1.1);
}

.wpp-flotante-contenido a {
    color: white;
    font-size: 1.5rem;
    text-decoration: none;
}

@media(max-width: 380px) {

    .header {
        gap: 1rem;
        padding: 1rem;

    }

    .header-menu ul {
        flex-direction: column;
        gap: .5rem;
    }


    .foto-personal {

        max-width: 70%;

    }

    .contenido-principal-home-botones {

        flex-direction: column;
        gap: 0;
    }

    .button-show-skills,
    .button-show-projects {

        padding: .5rem;
    }
}

@media(max-width:769px) {

    body {

        padding: 2rem;

    }

    .contenido-principal-home {

        flex-direction: column-reverse;
        gap: 4rem;
    }

    .foto-personal {

        max-width: 85%;

    }

    .contenido-principal-home-botones {
        flex-direction: column;
        gap: 0;
    }


}
